import React from 'react';
import styles from './CartTable.scss';
import classNames from 'classnames/bind';
import Button from 'components/common/Button';
import { Link } from 'react-router-dom';

const cx = classNames.bind(styles);

const CartTable = ({ver, cartList, totalPrice, onRemoveById}) => {
  const carts = cartList.map(
    (cart, i) => {
      return (
        <div className={cx('row')} key={i}>
            <div className={cx('content')}>
            <Link to={`/post/${cart.id}`}>
              <img
                className={cx('image')}
                src={`/api/uploads/${cart.thumbnailImage}`}/>
            </Link>
            </div>
            <div className={cx('content')}>
            <Link to={`/post/${cart.id}`}>
              {cart.title} - {cart.option}
              </Link>
            </div>
            <div className={cx('content')}>
              {cart.amount}
            </div>
            <div className={cx('content')}>
              {cart.totalPrice}
            </div>
            {
              !ver && <div className={cx('content')}>
                        <Button removeid={cart.cartId} onClick={onRemoveById} theme="remove"></Button>
                      </div>
            }
          </div>
      )
    }
  );

  const cartMobile = cartList.map(
    (cart, i) => {
      return (
        <div className={cx('line')} key={i}>
          <div className={cx('left-line')}>
            <div className={cx('thumbnail')}>
            <Link to={`/post/${cart.id}`}>
            <img
                className={cx('image')}
                src={`/api/uploads/${cart.thumbnailImage}`}/>
                </Link>
            </div>
            
          </div>
          <div className={cx('right-line')}>
            <div className={cx('title')}>
            <Link to={`/post/${cart.id}`}>
                {cart.title} - {cart.option}
                </Link>
            </div>
            <div className={cx('number')}>
              : {cart.amount}
            </div>
            <div className={cx('price')}>
              : {cart.totalPrice}
            </div>
          </div>
          {
            !ver && <div className={cx('remove-line')}>
                      <div className={cx('remove-mark')}>
                        <div className={cx('remove-button')} removeid={cart.cartId} onClick={onRemoveById}>
                          
                        </div>
                      </div>
                    </div>
          }
        </div>
      )
    }
  )
  if(cartList.length === 0) {
    return (
      <div className={cx('no-cart')}>
        <div className={cx('text')}>
              .
        </div>
        <div className={cx('button-wrapper')}>
          <Link to="/item" className={cx('button')}>
            
          </Link>
        </div>
      </div>
    )
  }
  return (
    <div className={cx('cart-table')}>
      <div className={cx('cart-content')}>
        <div className={cx('header')}>
          <div className={cx('heading')}>
            
          </div>
          <div className={cx('heading')}>
            
          </div>
          <div className={cx('heading')}>
            
          </div>
          <div className={cx('heading')}>
            
          </div>
          {
            !ver && <div className={cx('heading')}>
                      
                    </div>
          }
        </div>
        <div className={cx('body')}>
          {
            carts
          }
        </div>
        <div className={cx('total-price')}>
           : {parseInt(totalPrice, 10) + 3000} ()
        </div>
      </div>
      <div className={cx('cart-content-mobile')}>
          {
            cartMobile
          }
      </div>
      {
        !ver && <div className={cx('order-button')}>
          <Button to="/payment" theme="order-mobile"></Button>
        </div>
      }
      
    </div>
  );
}

export default CartTable;


